import { FormControl, InputLabel, MenuItem, Select, Typography } from "@material-ui/core";
import React, { Component } from "react";
import { connect } from "react-redux";
import { postData } from "../../actions/action";
import { continuousCall } from "../../constants/time.constants";
import { FETCH_VOTE_DATA } from "../../constants/types.constants";
import { createUrl, DELETE_RECO_URL, GET_VOTE_DATA_URL, POST_VOTE_URL } from "../../constants/url.constants";
import { ACCESS_FOOD_KEY } from "../../constants/user.constants";
import { voteFoodData } from "../../helpers/food.helper";
import { isAccessible } from "../../helpers/visibility.helper";
import VoteModel from "../../models/VoteModel";
import PermissionDenied from "../common/PermissionDenied";
import "./../common/common.css";
import Recommendation from "./recommendation/Recommendation";
import SimpleExpansionPanel from "./SimpleExpansionPanel";
import Vote from "./vote/Vote";

class Food extends Component {
  constructor(props) {
    super(props);
    this.state = {
      foodData: {},//todo remove from state
      week: props.week
    };
    this.vote = this.vote.bind(this);
    this.fetchAllFoodData = this.fetchAllFoodData.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.updateWeek = this.updateWeek.bind(this);
    this.deleteRecommendation = this.deleteRecommendation.bind(this);
  }

  vote(recommendationID, points) {
    const { foodData } = this.state;//todo remove
    const { profileObj, addVote, fetchVoteData } = this.props;
    const { week } = this.state;
    /* this.setState({//todo remove
      foodData: voteFoodData(recommendationID, foodData, points)
    }); */
    let vote = new VoteModel(
      recommendationID,
      profileObj.email,
      points,
      new Date()
    );
    addVote(vote);
    fetchVoteData(profileObj.email, week.startDate.value, week.endDate.value);
  }

  deleteRecommendation(id) {
    this.props.deleteRecommendationById(id);
    setTimeout(() => {
      this.fetchAllFoodData();
    }, 500);
  }

  handleChange(event) {
    this.setState({
      ...this.state,
      week: event.target.value
    });
  }

  updateWeek(week) {
    this.setState({ ...this.state, week });
  }

  fetchAllFoodData() {
    const { fetchVoteData, profileObj } = this.props;
    const { week } = this.state;
    fetchVoteData(profileObj.email, week.startDate.value, week.endDate.value);
  }

  componentDidMount() {
    clearInterval(this.foodInterval);
    this.foodInterval = continuousCall(this.fetchAllFoodData, this.props);
  }

  componentDidUpdate(prevProps, prevState) {
    const { voteData } = this.props;
    const { week } = this.state;
    if (week !== prevState.week) {
      this.componentDidMount();
    }
    if (voteData !== prevProps.voteData) {
      this.setState({ foodData: voteData });
    }
  }

  render() {
    const { foodData, week } = this.state;
    const { profileObj, currentWeek, lastWeek } = this.props;
    return isAccessible(profileObj, ACCESS_FOOD_KEY) ? (
      <div>
        <Typography paragraph>Food</Typography>
        <FormControl className="food-form">
          <InputLabel htmlFor="date-simple">Select Week</InputLabel>
          <Select
            value={week}
            onChange={this.handleChange}
            inputProps={{
              name: "date",
              id: "date-simple"
            }}
          >
            <MenuItem value={currentWeek}>
              This Week
              {/* {currentWeek.startDate} - {currentWeek.endDate} */}
            </MenuItem>
            <MenuItem value={lastWeek}>
              Previous Week
              {/* {lastWeek.startDate} - {lastWeek.endDate} */}
            </MenuItem>
          </Select>
        </FormControl>
        <SimpleExpansionPanel
          foodData={foodData}
          vote={this.vote}
          deleteRecommendation={this.deleteRecommendation}
          updateWeek={this.updateWeek}
          fetchAllFoodData={this.fetchAllFoodData}
          {...this.props}
        >
          <Recommendation panelHeader="Recommendation" />
          <Vote
            panelHeader="Vote"
            panelClassName="no-padding-expansion-panel"
          />
        </SimpleExpansionPanel>
      </div>
    ) : (
      <PermissionDenied profileObj={profileObj} />
    );
  }

  componentWillUnmount() {
    clearInterval(this.foodInterval);
  }
}

Food.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {
    voteData: state.voteReducer.voteData
  };
}

function mapDispatchToProps(dispatch) {
  return {
    deleteRecommendationById: id =>
      dispatch(postData(createUrl(DELETE_RECO_URL), { id })),
    fetchVoteData: (email, weekStartDate, weekEndDate) =>
      dispatch(
        postData(
          createUrl(GET_VOTE_DATA_URL),
          { email, week: { weekStartDate, weekEndDate } },
          FETCH_VOTE_DATA
        )
      ),
    addVote: vote => dispatch(postData(createUrl(POST_VOTE_URL), vote))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Food);
